import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../utils/AuthContext';
import { Link, useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../assets/SellerProducts.css';

const SellerProducts = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // Filters
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all'); // all, active, inactive
  const [sortBy, setSortBy] = useState('newest'); // newest, oldest, price-high, price-low, name
  const [viewMode, setViewMode] = useState('grid'); // grid, list
  
  // Pagination
  const [currentPage, setCurrentPage] = useState(1);
  const productsPerPage = 12;

  // Stats
  const [stats, setStats] = useState({
    total: 0,
    active: 0,
    inactive: 0,
    outOfStock: 0
  });

  useEffect(() => {
    if (user && user.user_type !== 'seller') {
      navigate('/dashboard');
    } else if (user) {
      fetchProducts();
    }
  }, [user, navigate]);

  const filterAndSortProducts = useCallback(() => {
    let filtered = [...products];

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.description?.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Status filter
    if (statusFilter === 'active') {
      filtered = filtered.filter(p => p.is_active);
    } else if (statusFilter === 'inactive') {
      filtered = filtered.filter(p => !p.is_active);
    }

    // Sort
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'newest':
          return new Date(b.created_at) - new Date(a.created_at);
        case 'oldest':
          return new Date(a.created_at) - new Date(b.created_at);
        case 'price-high':
          return b.price - a.price;
        case 'price-low':
          return a.price - b.price;
        case 'name':
          return a.name.localeCompare(b.name);
        default:
          return 0;
      }
    });

    setFilteredProducts(filtered);
    setCurrentPage(1); // Reset to first page when filters change
  }, [products, searchTerm, statusFilter, sortBy]);

  useEffect(() => {
    filterAndSortProducts();
  }, [filterAndSortProducts]);

  const fetchProducts = async () => {
    try {
      setLoading(true);
      setError(null);
      const token = localStorage.getItem('access_token');
      
      const response = await fetch('http://localhost:8000/api/seller/products/', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (!response.ok) throw new Error('Failed to fetch products');
      
      const data = await response.json();
      setProducts(data);
      
      // Calculate stats
      const newStats = {
        total: data.length,
        active: data.filter(p => p.is_active).length,
        inactive: data.filter(p => !p.is_active).length,
        outOfStock: data.filter(p => p.stock === 0).length
      };
      setStats(newStats);
      
    } catch (err) {
      console.error('Error fetching products:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleToggleStatus = async (productId, currentStatus) => {
    if (!window.confirm(`Bn c chc mun ${currentStatus ? 'n' : 'hin'} sn phm ny?`)) {
      return;
    }

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`http://localhost:8000/api/products/${productId}/toggle/`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ is_active: !currentStatus }),
      });

      if (!response.ok) throw new Error('Failed to update product');

      const updatedProduct = await response.json();
      
      // Update local state
      setProducts(products.map(p => 
        p.id === productId ? updatedProduct : p
      ));
      
      alert(` ${!currentStatus ? 'hin' : 'n'} sn phm thnh cng!`);
    } catch (err) {
      alert('Li khi cp nht sn phm: ' + err.message);
    }
  };

  const handleDeleteProduct = async (productId, productName) => {
    if (!window.confirm(`Bn c chc mun XA sn phm "${productName}"?\nHnh ng ny khng th hon tc!`)) {
      return;
    }

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`http://localhost:8000/api/products/${productId}/`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (!response.ok) throw new Error('Failed to delete product');

      // Remove from local state
      setProducts(products.filter(p => p.id !== productId));
      alert(' xa sn phm thnh cng!');
    } catch (err) {
      alert('Li khi xa sn phm: ' + err.message);
    }
  };

  // Pagination
  const indexOfLastProduct = currentPage * productsPerPage;
  const indexOfFirstProduct = indexOfLastProduct - productsPerPage;
  const currentProducts = filteredProducts.slice(indexOfFirstProduct, indexOfLastProduct);
  const totalPages = Math.ceil(filteredProducts.length / productsPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  if (loading) {
    return (
      <>
        <Header />
        <div className="seller-products-loading">
          <div className="spinner"></div>
          <p>ang ti sn phm...</p>
        </div>
        <Footer />
      </>
    );
  }

  return (
    <>
      
      <div className="seller-products-page">
        <div className="seller-products-container">
          
          {/* Page Header */}
          <div className="page-header">
            <div className="header-left">
              <Link to="/seller/dashboard" className="back-btn">
                 Dashboard
              </Link>
              <div>
                <h1> Qun l sn phm</h1>
                <p>Qun l tt c sn phm ca bn</p>
              </div>
            </div>
            <Link to="/seller/products/add" className="btn btn-primary" style={{ width: "230px" }}>
               Thm sn phm mi
            </Link>
          </div>

          {/* Stats Cards */}
          <div className="stats-row">
            <div className="stat-card">
              <div className="stat-icon"></div>
              <div className="stat-info">
                <h3>{stats.total}</h3>
                <p>Tng sn phm</p>
              </div>
            </div>
            <div className="stat-card active">
              <div className="stat-icon"></div>
              <div className="stat-info">
                <h3>{stats.active}</h3>
                <p>ang hot ng</p>
              </div>
            </div>
            <div className="stat-card inactive">
              <div className="stat-icon"></div>
              <div className="stat-info">
                <h3>{stats.inactive}</h3>
                <p> n</p>
              </div>
            </div>
            <div className="stat-card warning">
              <div className="stat-icon"></div>
              <div className="stat-info">
                <h3>{stats.outOfStock}</h3>
                <p>Ht hng</p>
              </div>
            </div>
          </div>

          {/* Filters & Actions */}
          <div className="filters-section">
            <div className="filters-row">
              {/* Search */}
              <div className="search-box">
                <span className="search-icon"></span>
                <input
                  type="text"
                  placeholder="Tm kim sn phm..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                {searchTerm && (
                  <button 
                    className="clear-search"
                    onClick={() => setSearchTerm('')}
                  >
                    
                  </button>
                )}
              </div>

              {/* Status Filter */}
              <select 
                className="filter-select"
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
              >
                <option value="all">Tt c trng thi</option>
                <option value="active">ang hot ng</option>
                <option value="inactive"> n</option>
              </select>

              {/* Sort */}
              <select 
                className="filter-select"
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
              >
                <option value="newest">Mi nht</option>
                <option value="oldest">C nht</option>
                <option value="price-high">Gi cao  thp</option>
                <option value="price-low">Gi thp  cao</option>
                <option value="name">Tn A  Z</option>
              </select>

              {/* View Mode */}
              <div className="view-mode-toggle">
                <button 
                  className={`view-btn ${viewMode === 'grid' ? 'active' : ''}`}
                  onClick={() => setViewMode('grid')}
                  title="Xem dng li"
                >
                  
                </button>
                <button 
                  className={`view-btn ${viewMode === 'list' ? 'active' : ''}`}
                  onClick={() => setViewMode('list')}
                  title="Xem dng danh sch"
                >
                  
                </button>
              </div>
            </div>

            {/* Results Info */}
            <div className="results-info">
              <p>
                Hin th <strong>{indexOfFirstProduct + 1}-{Math.min(indexOfLastProduct, filteredProducts.length)}</strong> 
                {' '}trong <strong>{filteredProducts.length}</strong> sn phm
              </p>
            </div>
          </div>

          {/* Error Message */}
          {error && (
            <div className="error-message">
               Li: {error}
              <button onClick={fetchProducts}>Th li</button>
            </div>
          )}

          {/* Products Grid/List */}
          {filteredProducts.length === 0 ? (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h2>Khng tm thy sn phm</h2>
              <p>
                {searchTerm || statusFilter !== 'all' 
                  ? 'Th thay i b lc hoc tm kim' 
                  : 'Bn cha c sn phm no'}
              </p>
              {!searchTerm && statusFilter === 'all' && (
                <Link to="/seller/products/add" className="btn btn-primary">
                   Thm sn phm u tin
                </Link>
              )}
            </div>
          ) : (
            <>
              <div className={`products-container ${viewMode}`}>
                {currentProducts.map((product) => (
                  <div key={product.id} className="product-card">
                    
                    {/* Product Image */}
                    <div className="product-image-wrapper">
                      <img 
                        src={product.image || '/placeholder.png'} 
                        alt={product.name}
                        className="product-image"
                      />
                      <div className="product-badges">
                        {product.stock === 0 && (
                          <span className="badge badge-danger">Ht hng</span>
                        )}
                        {!product.is_active && (
                          <span className="badge badge-secondary"> n</span>
                        )}
                        {product.is_active && product.stock > 0 && (
                          <span className="badge badge-success">ang bn</span>
                        )}
                      </div>
                    </div>

                    {/* Product Info */}
                    <div className="product-info">
                      <h3 className="product-name">{product.name}</h3>
                      
                      <p className="product-description">
                        {product.description?.substring(0, 100)}
                        {product.description?.length > 100 ? '...' : ''}
                      </p>

                      <div className="product-meta">
                        <div className="meta-item">
                          <span className="label">Gi:</span>
                          <span className="value price">${product.price}</span>
                        </div>
                        <div className="meta-item">
                          <span className="label">Kho:</span>
                          <span className={`value stock ${product.stock === 0 ? 'out-of-stock' : ''}`}>
                            {product.stock}
                          </span>
                        </div>
                        <div className="meta-item">
                          <span className="label">Danh mc:</span>
                          <span className="value">{product.category_name || 'N/A'}</span>
                        </div>
                        <div className="meta-item">
                          <span className="label">To lc:</span>
                          <span className="value">{new Date(product.created_at).toLocaleDateString('vi-VN')}</span>
                        </div>
                      </div>
                    </div>

                    {/* Product Actions */}
                    <div className="product-actions">
                      <Link 
                        to={`/products/${product.id}`}
                        className="action-btn view"
                        title="Xem chi tit"
                      >
                         Xem
                      </Link>
                      <Link 
                        to={`/seller/products/edit/${product.id}`}
                        className="action-btn edit"
                        title="Chnh sa"
                      >
                         Sa
                      </Link>
                      <button 
                        className={`action-btn toggle ${product.is_active ? 'hide' : 'show'}`}
                        onClick={() => handleToggleStatus(product.id, product.is_active)}
                        title={product.is_active ? 'n sn phm' : 'Hin sn phm'}
                      >
                        {product.is_active ? ' n' : ' Hin'}
                      </button>
                      <button 
                        className="action-btn delete"
                        onClick={() => handleDeleteProduct(product.id, product.name)}
                        title="Xa sn phm"
                      >
                         Xa
                      </button>
                    </div>

                  </div>
                ))}
              </div>

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="pagination">
                  <button 
                    className="page-btn"
                    onClick={() => paginate(currentPage - 1)}
                    disabled={currentPage === 1}
                  >
                     Trc
                  </button>
                  
                  <div className="page-numbers">
                    {[...Array(totalPages)].map((_, index) => {
                      const pageNumber = index + 1;
                      // Show first, last, current, and adjacent pages
                      if (
                        pageNumber === 1 ||
                        pageNumber === totalPages ||
                        (pageNumber >= currentPage - 1 && pageNumber <= currentPage + 1)
                      ) {
                        return (
                          <button
                            key={pageNumber}
                            className={`page-number ${currentPage === pageNumber ? 'active' : ''}`}
                            onClick={() => paginate(pageNumber)}
                          >
                            {pageNumber}
                          </button>
                        );
                      } else if (
                        pageNumber === currentPage - 2 ||
                        pageNumber === currentPage + 2
                      ) {
                        return <span key={pageNumber} className="page-dots">...</span>;
                      }
                      return null;
                    })}
                  </div>

                  <button 
                    className="page-btn"
                    onClick={() => paginate(currentPage + 1)}
                    disabled={currentPage === totalPages}
                  >
                    Sau 
                  </button>
                </div>
              )}
            </>
          )}

        </div>
      </div>

      <Footer />
    </>
  );
};

export default SellerProducts;