import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import ProductCard from '../components/ProductCard';
import Loading from '../components/Loading';
import '../assets/Home.css';
import Icon from '../components/Icon';
import usePageTitle from '../hooks/usePageTitle';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);

  usePageTitle('Trang ch');

  useEffect(() => {
    // Fetch products
    fetch('http://localhost:8000/api/products/')
      .then(res => res.json())
      .then(data => {
        setProducts(data.slice(0, 20));
      })
      .catch(err => console.error('Error loading products:', err));

    // Fetch categories
    fetch('http://localhost:8000/api/categories/')
      .then(res => res.json())
      .then(data => {
        const items = Array.isArray(data) ? data : (data.results || []);
        const sorted = [...items].sort((a, b) => (b.product_count || 0) - (a.product_count || 0));
        setCategories(sorted.slice(0, 6));
        setLoading(false);
      })
      .catch(err => {
        console.error('Error loading categories:', err);
        setLoading(false);
      });
  }, []);

  return (
    <div className="home-page">
      {loading ? (
        <Loading message="ang ti trang ch..." />
      ) : (
        <>
          {/* Hero Section */}
          <section className="hero-section">
            <div className="hero-content">
              <h1 className="hero-title">Cho mng n vi Sellify</h1>
              <p className="hero-subtitle">
                Khm ph hng ngn sn phm cht lng cao vi gi tt nht. 
                Mua sm d dng, giao hng nhanh chng.
              </p>
              <div className="hero-cta">
                <Link to="/products" className="hero-btn hero-btn-primary">
                  Khm ph ngay &rarr;
                </Link>
                <Link to="/about" className="hero-btn hero-btn-secondary">
                  Tm hiu thm
                </Link>
              </div>
            </div>
          </section>

          {/* Featured Categories */}
          {!loading && categories.length > 0 && (
            <section className="featured-categories">
              <div className="section-header">
                <h2 className="section-title">Danh mc ni bt</h2>
                <p className="section-subtitle">
                  Khm ph cc danh mc sn phm phong ph v a dng
                </p>
              </div>
              <div className="categories-grid">
                {categories.map((category) => (
                  <Link
                    key={category.id}
                    to={`/category/${category.id}`}
                    className="category-card"
                  >
                    <span className="category-icon">
                      <Icon name="box-open" size={20} />
                    </span>
                    <h3 className="category-name">{category.name}</h3>
                    <p className="category-count">
                      {category.product_count || 0} sn phm
                    </p>
                  </Link>
                ))}
              </div>
            </section>
          )}

          {/* Featured Products */}
          {!loading && products.length > 0 && (
            <section className="featured-products">
              <div className="products-container">
                <div className="section-header">
                  <h2 className="section-title">Sn phm ni bt</h2>
                  <p className="section-subtitle">
                    Nhng sn phm c yu thch nht ti Sellify
                  </p>
                </div>
                <div className="products-grid">
                  {products.map((product) => (
                    <ProductCard key={product.id} product={product} />
                  ))}
                </div>
              </div>
            </section>
          )}

          {/* Features Section */}
          <section className="features-section">
            <div className="section-header">
              <h2 className="section-title">Ti sao chn chng ti?</h2>
              <p className="section-subtitle">
                Nhng l do khin khch hng tin tng v la chn PBL6 Shop
              </p>
            </div>
            <div className="features-grid">
              <div className="feature-card">
                <span className="feature-icon">
                  <Icon name="truck-fast" size={22} />
                </span>
                <h3 className="feature-title">Giao hng nhanh</h3>
                <p className="feature-description">
                  Giao hng ton quc, nhn hng trong 2-3 ngy vi dch v vn chuyn uy tn
                </p>
              </div>
              <div className="feature-card">
                <span className="feature-icon">
                  <Icon name="medal" size={22} />
                </span>
                <h3 className="feature-title">Cht lng m bo</h3>
                <p className="feature-description">
                  100% sn phm chnh hng, c kim tra k cng trc khi giao
                </p>
              </div>
              <div className="feature-card">
                <span className="feature-icon">
                  <Icon name="lock" size={22} />
                </span>
                <h3 className="feature-title">Thanh ton an ton</h3>
                <p className="feature-description">
                  H tr nhiu hnh thc thanh ton, bo mt thng tin tuyt i
                </p>
              </div>
            </div>
          </section>

          {/* Stats Section */}
          <section className="stats-section">
            <div className="stats-grid">
              <div className="stat-card">
                <span className="stat-number">10K+</span>
                <span className="stat-label">Sn phm</span>
              </div>
              <div className="stat-card">
                <span className="stat-number">50K+</span>
                <span className="stat-label">Khch hng</span>
              </div>
              <div className="stat-card">
                <span className="stat-number">100+</span>
                <span className="stat-label">i tc</span>
              </div>
              <div className="stat-card">
                <span className="stat-number">99%</span>
                <span className="stat-label">Hi lng</span>
              </div>
            </div>
          </section>

          {/* CTA Section */}
          <section className="cta-section">
            <div className="cta-content">
              <h2 className="cta-title">Bt u mua sm ngay hm nay</h2>
              <p className="cta-text">
                ng k ti khon  nhn nhiu u i hp dn v tri nghim mua sm tuyt vi
              </p>
              <Link to="/register" className="cta-btn">
                ng k min ph
              </Link>
            </div>
          </section>
        </>
      )}
    </div>
  );
}