import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import '../assets/OrderSuccess.css';
import Icon from '../components/Icon';
import usePageTitle from '../hooks/usePageTitle';

export default function OrderSuccess() {
  const location = useLocation();
  const orderId = location.state?.orderId;

  usePageTitle('t hng thnh cng');

  return (
    <div className="order-success-page">
      <div className="success-container">
        <div className="success-icon">
          <Icon name="circle-check" size={42} />
        </div>
        <h1>t hng thnh cng!</h1>
        <p className="success-message">
          Cm n bn  t hng. Chng ti s lin h vi bn sm nht  xc nhn n hng.
        </p>
        
        {orderId && (
          <div className="order-info">
            <p>M n hng ca bn: <strong>#{orderId}</strong></p>
          </div>
        )}

        <div className="success-actions">
          <Link to="/" className="btn-primary">
            Tip tc mua sm
          </Link>
          {orderId && (
            <Link to={`/orders/${orderId}`} className="btn-secondary">
              Xem n hng
            </Link>
          )}
        </div>
      </div>
    </div>
  );
}