import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useCart } from "../utils/CartContext";
import { useAuth } from "../utils/AuthContext";
import { formatPrice } from "../utils/formatPrice";
import {
  getCities,
  getDistrictsByCity,
  getWardsByDistrict,
} from "../utils/locationsData";
import "../assets/Checkout.css";
import usePageTitle from "../hooks/usePageTitle";

const API_BASE = process.env.REACT_APP_API_BASE || "http://localhost:8000";

async function safeFetchJSON(url, options = {}) {
  const res = await fetch(url, options);
  const ct = res.headers.get("content-type") || "";
  if (!res.ok) {
    const txt = await res.text().catch(() => "");
    throw new Error(`HTTP ${res.status} - ${txt.slice(0, 200)}`);
  }
  if (!ct.includes("application/json")) {
    const txt = await res.text().catch(() => "");
    throw new Error("Server tr v non-JSON: " + txt.slice(0, 200));
  }
  return res.json();
}

export default function Checkout() {
  const navigate = useNavigate();
  const { cartItems, clearCart, getCartTotal } = useCart();
  const { user } = useAuth();

  usePageTitle("Thanh ton");

  const [formData, setFormData] = useState({
    fullName: "",
    phone: "",
    email: "",
    address: "",
    city: "",
    district: "",
    ward: "",
    paymentMethod: "cod",
    notes: "",
  });

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [cities, setCities] = useState([]);
  const [districts, setDistricts] = useState([]);
  const [wards, setWards] = useState([]);

  useEffect(() => {
    if (cartItems.length === 0) {
      navigate("/cart");
      return;
    }

    if (user) {
      setFormData((prev) => ({
        ...prev,
        fullName: user.full_name || "",
        phone: user.phone || "",
        email: user.email || "",
      }));

      const token = localStorage.getItem("access_token");
      if (token) {
        safeFetchJSON(`${API_BASE}/api/user/address/`, {
          headers: { Authorization: `Bearer ${token}` },
        })
          .then((data) => {
            if (data && (data.address || data.city)) {
              setFormData((prev) => ({
                ...prev,
                address: data.address || "",
                city: data.city || "",
                district: data.district || "",
                ward: data.ward || "",
              }));
            }
          })
          .catch((err) => console.error("Error loading address:", err.message));
      }
    }

    setCities(getCities());
  }, [cartItems, user, navigate]);

  useEffect(() => {
    if (formData.city) {
      setDistricts(getDistrictsByCity(formData.city));
    } else {
      setDistricts([]);
      setWards([]);
    }
  }, [formData.city]);

  useEffect(() => {
    if (formData.district) {
      setWards(getWardsByDistrict(formData.district));
    } else {
      setWards([]);
    }
  }, [formData.district]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (errors[name]) {
      setErrors((prev) => ({
        ...prev,
        [name]: "",
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.fullName.trim()) newErrors.fullName = "Vui lng nhp h tn";
    if (!formData.phone.trim()) newErrors.phone = "Vui lng nhp s in thoi";
    else if (!/^[0-9]{10,11}$/.test(formData.phone.trim()))
      newErrors.phone = "S in thoi khng hp l";
    if (!formData.email.trim()) newErrors.email = "Vui lng nhp email";
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email))
      newErrors.email = "Email khng hp l";
    if (!formData.address.trim()) newErrors.address = "Vui lng nhp a ch";
    if (!formData.city) newErrors.city = "Vui lng chn tnh/thnh ph";
    if (!formData.district) newErrors.district = "Vui lng chn qun/huyn";
    if (!formData.ward) newErrors.ward = "Vui lng chn phng/x";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) return;
    if (getCartTotal() <= 0 || cartItems.length === 0) return;

    setLoading(true);
    try {
      const totalAmount = Math.round(Number(getCartTotal()) || 0);

      const orderData = {
        full_name: formData.fullName,
        phone: formData.phone,
        email: formData.email,
        address: formData.address,
        ward: formData.ward,
        district: formData.district,
        city: formData.city,
        payment_method: formData.paymentMethod,
        notes: formData.notes,
        items: cartItems.map((item) => ({
          product_id: item.id,
          quantity: item.quantity,
          color: item.color || "",
          size: item.size || "",
          price: Number(item.price),
        })),
        total_amount: totalAmount,
      };

      const token = localStorage.getItem("access_token");
      const headers = {
        "Content-Type": "application/json",
        ...(token ? { Authorization: `Bearer ${token}` } : {}),
      };

      if (formData.paymentMethod === "vnpay") {
        // 1) Create order first
        const created = await safeFetchJSON(`${API_BASE}/api/orders/`, {
          method: "POST",
          headers,
          body: JSON.stringify(orderData),
        });
        if (!created?.success || !created?.order?.order_id) {
          throw new Error(created?.message || "Khng to c n hng");
        }
        const orderId = created.order.order_id;
        const amount = Math.round(
          Number(created.order.total_amount ?? totalAmount)
        );

        // 2) Request VNPay payment link with order_id + amount
        const pay = await safeFetchJSON(`${API_BASE}/api/payment/vnpay/`, {
          method: "POST",
          headers,
          body: JSON.stringify({ order_id: orderId, amount: created.order.total_amount }),
        });
        if (pay?.payment_url) {
          // Clear cart after successful callback, not here
          window.location.href = pay.payment_url;
          return;
        }
        throw new Error(pay?.message || "Khng to c lin kt VNPay.");
      }

      // COD or other methods
      const result = await safeFetchJSON(`${API_BASE}/api/orders/`, {
        method: "POST",
        headers,
        body: JSON.stringify(orderData),
      });

      if (!result.success || !result.order?.order_id) {
        throw new Error(result.message || "t hng tht bi");
      }

      clearCart();
      navigate("/order-success", { state: { orderId: result.order.order_id } });
    } catch (error) {
      console.error(" Error submitting order:", error);
      let errorMessage = "C li xy ra khi t hng. Vui lng th li!";
      if (/Failed to fetch/i.test(error.message))
        errorMessage = "Khng th kt ni n server. Hy kim tra backend.";
      else if (/non-JSON|Server tr v non-JSON/i.test(error.message))
        errorMessage = "Backend tr v d liu khng hp l.";
      else if (error.message) errorMessage = error.message;

      alert(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  if (cartItems.length === 0) return null;

  return (
    <div className="checkout-page">
      <div className="checkout-container">
        <div className="checkout-header">
          <h1>Thanh ton</h1>
          <div className="checkout-steps">
            <div className="step active">1. Thng tin giao hng</div>
            <div className="step">2. Xc nhn n hng</div>
            <div className="step">3. Hon tt</div>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="checkout-form">
          <div className="checkout-content">
            <div className="shipping-info">
              <h2>Thng tin giao hng</h2>
              {/* --- Form thng tin ngi nhn --- */}
              {/* H tn, in thoi */}
              <div className="form-row">
                <div className="form-group">
                  <label>H v tn *</label>
                  <input
                    type="text"
                    name="fullName"
                    value={formData.fullName}
                    onChange={handleInputChange}
                    className={errors.fullName ? "error" : ""}
                    placeholder="Nhp h v tn"
                  />
                  {errors.fullName && (
                    <span className="error-message">{errors.fullName}</span>
                  )}
                </div>
                <div className="form-group">
                  <label>S in thoi *</label>
                  <input
                    type="tel"
                    name="phone"
                    value={formData.phone}
                    onChange={handleInputChange}
                    className={errors.phone ? "error" : ""}
                    placeholder="Nhp s in thoi"
                  />
                  {errors.phone && (
                    <span className="error-message">{errors.phone}</span>
                  )}
                </div>
              </div>

              {/* Email */}
              <div className="form-group">
                <label>Email *</label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  className={errors.email ? "error" : ""}
                  placeholder="Nhp email"
                />
                {errors.email && (
                  <span className="error-message">{errors.email}</span>
                )}
              </div>

              {/* a ch */}
              <div className="form-row">
                <div className="form-group">
                  <label>Tnh/Thnh ph *</label>
                  <select
                    name="city"
                    value={formData.city}
                    onChange={handleInputChange}
                    className={errors.city ? "error" : ""}
                  >
                    <option value="">Chn tnh/thnh ph</option>
                    {cities.map((c) => (
                      <option key={c.id} value={c.id}>
                        {c.name}
                      </option>
                    ))}
                  </select>
                  {errors.city && (
                    <span className="error-message">{errors.city}</span>
                  )}
                </div>

                <div className="form-group">
                  <label>Qun/Huyn *</label>
                  <select
                    name="district"
                    value={formData.district}
                    onChange={handleInputChange}
                    className={errors.district ? "error" : ""}
                    disabled={!formData.city}
                  >
                    <option value="">Chn qun/huyn</option>
                    {districts.map((d) => (
                      <option key={d.id} value={d.id}>
                        {d.name}
                      </option>
                    ))}
                  </select>
                  {errors.district && (
                    <span className="error-message">{errors.district}</span>
                  )}
                </div>
              </div>

              <div className="form-group">
                <label>Phng/X *</label>
                <select
                  name="ward"
                  value={formData.ward}
                  onChange={handleInputChange}
                  className={errors.ward ? "error" : ""}
                  disabled={!formData.district}
                >
                  <option value="">Chn phng/x</option>
                  {wards.map((w) => (
                    <option key={w.id} value={w.id}>
                      {w.name}
                    </option>
                  ))}
                </select>
                {errors.ward && (
                  <span className="error-message">{errors.ward}</span>
                )}
              </div>

              <div className="form-group">
                <label>a ch chi tit *</label>
                <textarea
                  name="address"
                  value={formData.address}
                  onChange={handleInputChange}
                  className={errors.address ? "error" : ""}
                  placeholder="S nh, tn ng..."
                />
                {errors.address && (
                  <span className="error-message">{errors.address}</span>
                )}
              </div>

              <div className="form-group">
                <label>Ghi ch</label>
                <textarea
                  name="notes"
                  value={formData.notes}
                  onChange={handleInputChange}
                  placeholder="Ghi ch thm..."
                />
              </div>
            </div>

            {/* --- Tm tt n hng --- */}
            <div className="order-summary">
              <h2>n hng ca bn</h2>
              <div className="order-items">
                {cartItems.map((item, idx) => {
                  const availableColors = Array.isArray(item.colorOptions) ? item.colorOptions : [];
                  const availableSizes = Array.isArray(item.sizeOptions) ? item.sizeOptions : [];
                  return (
                    <div key={idx} className="order-item">
                      <img
                        src={
                          item.image ||
                          process.env.PUBLIC_URL + "/default-product.png"
                        }
                        alt={item.name}
                      />
                      <div className="item-info">
                        <h4>{item.name}</h4>
                        <div className="item-variants">
                          {item.color && <span>Mu: {item.color}</span>}
                          {!item.color && availableColors.length > 0 && (
                            <span>Mu c: {availableColors.join(', ')}</span>
                          )}
                          {item.size && <span>Size: {item.size}</span>}
                          {!item.size && availableSizes.length > 0 && (
                            <span>Size c: {availableSizes.join(', ')}</span>
                          )}
                        </div>
                        <div>S lng: {item.quantity}</div>
                      </div>
                      <div className="item-price">
                        {formatPrice(item.price * item.quantity)}
                      </div>
                    </div>
                  );
                })}
              </div>

              {/* --- Phng thc thanh ton --- */}
              <div className="payment-method">
                <h3>Phng thc thanh ton</h3>
                <div className="payment-options">
                  <label className="payment-option">
                    <input
                      type="radio"
                      name="paymentMethod"
                      value="cod"
                      checked={formData.paymentMethod === "cod"}
                      onChange={handleInputChange}
                    />
                    <span>Thanh ton khi nhn hng (COD)</span>
                  </label>
                  <label className="payment-option">
                    <input
                      type="radio"
                      name="paymentMethod"
                      value="vnpay"
                      checked={formData.paymentMethod === "vnpay"}
                      onChange={handleInputChange}
                    />
                    <span>Thanh ton qua VNPay</span>
                  </label>
                  <label className="payment-option">
                    <input
                      type="radio"
                      name="paymentMethod"
                      value="momo"
                      checked={formData.paymentMethod === "momo"}
                      onChange={handleInputChange}
                    />
                    <span>V MoMo</span>
                  </label>
                </div>
              </div>

              {/* --- Tng cng --- */}
              <div className="order-total">
                <div className="total-row">
                  <span>Tm tnh:</span>
                  <span>{formatPrice(getCartTotal())}</span>
                </div>
                <div className="total-row">
                  <span>Ph vn chuyn:</span>
                  <span className="free-shipping">Min ph</span>
                </div>
                <div className="total-row final">
                  <span>Tng cng:</span>
                  <span>{formatPrice(getCartTotal())}</span>
                </div>
              </div>

              <button
                type="submit"
                className="place-order-btn"
                disabled={loading}
              >
                {loading ? "ang x l..." : "t hng"}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
