import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import '../assets/AddProduct.css';

const AddProduct = () => {
  const navigate = useNavigate();

  const [form, setForm] = useState({
    name: '',
    description: '',
    price: '',
    stock: '',
    imageFile: null,
    category: '',
  });
  const [imagePreview, setImagePreview] = useState(null);
  const [categories, setCategories] = useState([]);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [loading, setLoading] = useState(false);
  const [loadingCats, setLoadingCats] = useState(true);
  const [catError, setCatError] = useState(null);

  useEffect(() => {
    let cancelled = false;

    async function loadCategories() {
      setLoadingCats(true);
      setCatError(null);
      try {
        const res = await axios.get('http://localhost:8000/api/categories/', { timeout: 10000 });
        const data = res.data;
        const list = Array.isArray(data) ? data : (data.results ?? []);
        if (!cancelled) setCategories(list);
      } catch (err) {
        console.error('loadCategories error', err);
        if (!cancelled) setCatError(err.response?.data ?? err.message ?? 'Li');
      } finally {
        if (!cancelled) setLoadingCats(false);
      }
    }

    loadCategories();
    return () => { cancelled = true; };
  }, []);

  const handleChange = (e) => {
    setMessage({ type: '', text: '' });
    const { name, value, files } = e.target;

    if (name === 'imageFile') {
      const file = files[0];
      setForm((prev) => ({ ...prev, imageFile: file }));

      if (file) {
        const reader = new FileReader();
        reader.onloadend = () => setImagePreview(reader.result);
        reader.readAsDataURL(file);
      } else {
        setImagePreview(null);
      }
    } else {
      setForm((prev) => ({ ...prev, [name]: value }));
    }
  };

  const removeImage = () => {
    setForm((prev) => ({ ...prev, imageFile: null }));
    setImagePreview(null);
  };

  const validate = () => {
    if (!form.name.trim()) return 'Tn sn phm khng c  trng';
    if (form.name.length < 3) return 'Tn sn phm phi c t nht 3 k t';
    if (!form.price || Number(form.price) <= 0) return 'Gi phi ln hn 0';
    if (form.stock === '' || Number(form.stock) < 0) return 'S lng phi >= 0';
    if (!form.category) return 'Vui lng chn danh mc';
    return '';
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ type: '', text: '' });

    const err = validate();
    if (err) {
      setMessage({ type: 'error', text: err });
      setLoading(false);
      return;
    }

    const token = localStorage.getItem('access_token');
    const formData = new FormData();
    formData.append('name', form.name.trim());
    formData.append('description', form.description.trim());
    formData.append('price', Number(form.price));
    formData.append('stock', Number(form.stock));
    formData.append('category', form.category);
    if (form.imageFile) formData.append('image', form.imageFile);

    try {
      await axios.post('http://localhost:8000/api/products/', formData, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'multipart/form-data',
        },
      });

      setMessage({ type: 'success', text: ' To sn phm thnh cng!' });
      setTimeout(() => navigate('/seller/products'), 1500);
    } catch (error) {
      const errorMsg = error.response?.data?.message || error.message || 'C li xy ra';
      setMessage({ type: 'error', text: errorMsg });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="add-product-wrapper">
      <div className="add-product-page">
        <div className="add-product-container">

          {/* Header */}
          <div className="add-product-header">
            <span className="icon"></span>
            <h1>Thm Sn Phm Mi</h1>
            <p>in thng tin sn phm ca bn</p>
          </div>

          {/* Form */}
          <div className="add-product-form">
            <Link to="/seller/products" className="back-link">
               Quay li Danh sch sn phm
            </Link>

            <div className="required-fields-note">
              Cc trng nh du <span className="required">*</span> l bt buc
            </div>

            {message.text && (
              <div className={`alert alert-${message.type}`}>
                {message.text}
              </div>
            )}

            <form onSubmit={handleSubmit}>
              {/* Product Name */}
              <div className="form-group">
                <label className="form-label">
                  Tn sn phm <span className="required">*</span>
                </label>
                <input
                  type="text"
                  name="name"
                  className="form-input"
                  placeholder="Nhp tn sn phm..."
                  value={form.name}
                  onChange={handleChange}
                  disabled={loading}
                  maxLength={200}
                />
                <span className="char-counter">
                  {form.name.length}/200 k t
                </span>
              </div>

              {/* Category */}
              <div className="form-group">
                <label className="form-label">
                  Danh mc <span className="required">*</span>
                </label>
                <select
                  name="category"
                  className="form-select"
                  value={form.category}
                  onChange={handleChange}
                  disabled={loading || loadingCats}
                >
                  <option value="">-- Chn danh mc --</option>
                  {categories.map((c) => (
                    <option key={c.id} value={c.id}>
                      {c.name}
                    </option>
                  ))}
                </select>
                {loadingCats && (
                  <div className="loading-text">
                    <span className="loading-spinner"></span>
                    ang ti danh mc...
                  </div>
                )}
                {catError && (
                  <div className="category-error">
                     Li ti danh mc: {String(catError)}
                  </div>
                )}
              </div>

              {/* Description */}
              <div className="form-group">
                <label className="form-label">
                  M t sn phm
                  <span className="form-label-optional">(Khng bt buc)</span>
                </label>
                <textarea
                  name="description"
                  className="form-input"
                  placeholder="Nhp m t chi tit v sn phm..."
                  rows={5}
                  value={form.description}
                  onChange={handleChange}
                  disabled={loading}
                  maxLength={1000}
                />
                <span className="char-counter">
                  {form.description.length}/1000 k t
                </span>
              </div>

              {/* Price & Stock */}
              <div className="form-row">
                <div className="form-group">
                  <label className="form-label">
                    Gi <span className="required">*</span>
                  </label>
                  <div className="input-group">
                    <span className="input-icon"></span>
                    <input
                      type="number"
                      step="0.01"
                      min="0"
                      name="price"
                      className="form-input"
                      placeholder="0.00"
                      value={form.price}
                      onChange={handleChange}
                      disabled={loading}
                    />
                  </div>
                  <span className="helper-text">n v: USD</span>
                </div>

                <div className="form-group">
                  <label className="form-label">
                    S lng <span className="required">*</span>
                  </label>
                  <div className="input-group">
                    <span className="input-icon"></span>
                    <input
                      type="number"
                      min="0"
                      name="stock"
                      className="form-input"
                      placeholder="0"
                      value={form.stock}
                      onChange={handleChange}
                      disabled={loading}
                    />
                  </div>
                  <span className="helper-text">S lng cn trong kho</span>
                </div>
              </div>

              {/* Image Upload */}
              <div className="form-group">
                <label className="form-label">
                  Hnh nh sn phm
                  <span className="form-label-optional">(Khng bt buc)</span>
                </label>
                
                {!imagePreview ? (
                  <label className="file-input-label">
                    <span className="icon"></span>
                    <div>
                      <strong>Chn nh sn phm</strong>
                      <br />
                      <small>PNG, JPG, GIF (Max 5MB)</small>
                    </div>
                    <input
                      type="file"
                      name="imageFile"
                      accept="image/*"
                      onChange={handleChange}
                      disabled={loading}
                    />
                  </label>
                ) : (
                  <div className="file-preview">
                    <img src={imagePreview} alt="Preview" />
                    <div className="file-name">
                      <span className="icon"></span>
                      <span>{form.imageFile?.name}</span>
                      <button
                        type="button"
                        className="remove-file-btn"
                        onClick={removeImage}
                        title="Xa nh"
                      >
                        
                      </button>
                    </div>
                  </div>
                )}
              </div>

              {/* Submit Button */}
              <div className="form-footer">
                <button 
                  type="submit" 
                  className="btn btn-primary" 
                  disabled={loading}
                >
                  {loading ? (
                    <>
                      <span className="loading-spinner"></span>
                      ang to sn phm...
                    </>
                  ) : (
                    <> To sn phm</>
                  )}
                </button>

                <button
                  type="button"
                  className="btn btn-secondary"
                  onClick={() => navigate('/seller/products')}
                  disabled={loading}
                >
                  Hy b
                </button>

                <p className="form-footer-text">
                  Cn tr gip? <a href="/help">Xem hng dn</a>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddProduct;
