import React, { useState, useEffect } from 'react';
import { useAuth } from '../utils/AuthContext';
import { Link, useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../assets/SellerDashboard.css';

const SellerDashboard = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  // States
  const [stats, setStats] = useState({
    totalProducts: 0,
    activeProducts: 0,
    totalOrders: 0,
    pendingOrders: 0,
    totalRevenue: 0,
    monthRevenue: 0,
  });
  const [products, setProducts] = useState([]);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('overview'); // overview, products, orders

  // Check if user is seller
  useEffect(() => {
    if (user && user.user_type !== 'seller') {
      navigate('/dashboard');
    }
  }, [user, navigate]);

  // Fetch seller data
  useEffect(() => {
    if (user && user.user_type === 'seller') {
      fetchSellerData();
    }
  }, [user]);

  const fetchSellerData = async () => {
    try {
      setLoading(true);
      const token = localStorage.getItem('token');
      
      // Fetch stats
      const statsRes = await fetch('http://localhost:8000/api/seller/stats/', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });
      
      if (statsRes.ok) {
        const statsData = await statsRes.json();
        setStats(statsData);
      }

      // Fetch products
      const productsRes = await fetch('http://localhost:8000/api/seller/products/', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });
      
      if (productsRes.ok) {
        const productsData = await productsRes.json();
        setProducts(productsData.slice(0, 5)); // Top 5 products
      }

      // Fetch orders
      const ordersRes = await fetch('http://localhost:8000/api/seller/orders/', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });
      
      if (ordersRes.ok) {
        const ordersData = await ordersRes.json();
        setOrders(ordersData.slice(0, 5)); // Recent 5 orders
      }

    } catch (error) {
      console.error('Error fetching seller data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleProductStatusToggle = async (productId, currentStatus) => {
    const token = localStorage.getItem('token');
    try {
      const response = await fetch(`http://localhost:8000/api/products/${productId}/`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ is_active: !currentStatus }),
      });

      if (response.ok) {
        fetchSellerData(); // Refresh data
        alert(' cp nht trng thi sn phm');
      }
    } catch (error) {
      console.error('Error updating product:', error);
      alert('Li khi cp nht sn phm');
    }
  };

  const handleOrderStatusUpdate = async (orderId, newStatus) => {
    const token = localStorage.getItem('token');
    try {
      const response = await fetch(`http://localhost:8000/api/orders/${orderId}/`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: newStatus }),
      });

      if (response.ok) {
        fetchSellerData();
        alert(' cp nht trng thi n hng');
      }
    } catch (error) {
      console.error('Error updating order:', error);
      alert('Li khi cp nht n hng');
    }
  };

  if (loading) {
    return (
      <>
        <Header />
        <div className="seller-dashboard-loading">
          <div className="spinner"></div>
          <p>ang ti d liu...</p>
        </div>
        <Footer />
      </>
    );
  }

  return (
    <>
      <div className="seller-dashboard">
        <div className="dashboard-container">
          
          {/* Dashboard Header */}
          <div className="dashboard-header">
            <div className="header-content">
              <h1> Seller Dashboard</h1>
              <p className="welcome-text">
                Cho mng tr li, <strong>{user?.full_name || user?.username}</strong>!
              </p>
            </div>
            <div className="header-actions">
              <Link to="/seller/products/new" className="btn btn-primary">
                 Thm sn phm mi
              </Link>
            </div>
          </div>

          {/* Stats Cards */}
          <div className="stats-grid">
            <div className="stat-card stat-products">
              <div className="stat-icon"></div>
              <div className="stat-content">
                <h3>{stats.totalProducts}</h3>
                <p>Tng sn phm</p>
                <span className="stat-detail">{stats.activeProducts} ang hot ng</span>
              </div>
            </div>

            <div className="stat-card stat-orders">
              <div className="stat-icon"></div>
              <div className="stat-content">
                <h3>{stats.totalOrders}</h3>
                <p>Tng n hng</p>
                <span className="stat-detail">{stats.pendingOrders} ang ch x l</span>
              </div>
            </div>

            <div className="stat-card stat-revenue">
              <div className="stat-icon"></div>
              <div className="stat-content">
                <h3>${stats.totalRevenue.toLocaleString()}</h3>
                <p>Tng doanh thu</p>
                <span className="stat-detail">+${stats.monthRevenue.toLocaleString()} thng ny</span>
              </div>
            </div>

            <div className="stat-card stat-rating">
              <div className="stat-icon"></div>
              <div className="stat-content">
                <h3>4.8</h3>
                <p>nh gi trung bnh</p>
                <span className="stat-detail">124 nh gi</span>
              </div>
            </div>
          </div>

          {/* Tabs Navigation */}
          <div className="dashboard-tabs">
            <button 
              className={`tab ${activeTab === 'overview' ? 'active' : ''}`}
              onClick={() => setActiveTab('overview')}
            >
               Tng quan
            </button>
            <button 
              className={`tab ${activeTab === 'products' ? 'active' : ''}`}
              onClick={() => setActiveTab('products')}
            >
               Sn phm
            </button>
            <button 
              className={`tab ${activeTab === 'orders' ? 'active' : ''}`}
              onClick={() => setActiveTab('orders')}
            >
               n hng
            </button>
          </div>

          {/* Tab Content */}
          <div className="dashboard-content">
            
            {/* OVERVIEW TAB */}
            {activeTab === 'overview' && (
              <div className="overview-tab">
                <div className="content-grid">
                  
                  {/* Quick Actions */}
                  <div className="dashboard-card quick-actions-card">
                    <h2 className="card-title"> Thao tc nhanh</h2>
                    <div className="quick-actions">
                      <Link to="/seller/products/new" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Thm sn phm</strong>
                          <small>ng sn phm mi</small>
                        </div>
                      </Link>
                      
                      <Link to="/seller/products" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Qun l sn phm</strong>
                          <small>Xem v chnh sa</small>
                        </div>
                      </Link>
                      
                      <Link to="/seller/orders" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Xem n hng</strong>
                          <small>Qun l n t</small>
                        </div>
                      </Link>
                      
                      <Link to="/seller/analytics" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Thng k</strong>
                          <small>Xem bo co</small>
                        </div>
                      </Link>
                      
                      <Link to="/profile" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Ci t</strong>
                          <small>Chnh sa h s</small>
                        </div>
                      </Link>
                      
                      <Link to="/seller/promotions" className="action-item">
                        <span className="action-icon"></span>
                        <div className="action-text">
                          <strong>Khuyn mi</strong>
                          <small>To u i</small>
                        </div>
                      </Link>
                    </div>
                  </div>

                  {/* Recent Products */}
                  <div className="dashboard-card">
                    <div className="card-header">
                      <h2 className="card-title"> Sn phm gn y</h2>
                      <Link to="/seller/products" className="view-all-link">
                        Xem tt c 
                      </Link>
                    </div>
                    <div className="products-list">
                      {products.length > 0 ? (
                        products.map(product => (
                          <div key={product.product_id} className="product-item">
                            <img 
                              src={product.image || '/placeholder.png'} 
                              alt={product.name}
                              className="product-thumb"
                            />
                            <div className="product-info">
                              <h4>{product.name}</h4>
                              <p className="product-price">${product.price}</p>
                              <span className={`product-status ${product.is_active ? 'active' : 'inactive'}`}>
                                {product.is_active ? ' ang bn' : '  n'}
                              </span>
                            </div>
                            <div className="product-actions">
                              <button 
                                className="btn-icon"
                                onClick={() => navigate(`/seller/products/edit/${product.product_id}`)}
                                title="Chnh sa"
                              >
                                
                              </button>
                              <button 
                                className="btn-icon"
                                onClick={() => handleProductStatusToggle(product.product_id, product.is_active)}
                                title={product.is_active ? 'n sn phm' : 'Hin th sn phm'}
                              >
                                {product.is_active ? '' : ''}
                              </button>
                            </div>
                          </div>
                        ))
                      ) : (
                        <div className="empty-state">
                          <p> Cha c sn phm no</p>
                          <Link to="/seller/products/new" className="btn btn-primary">
                            Thm sn phm u tin
                          </Link>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Recent Orders */}
                  <div className="dashboard-card">
                    <div className="card-header">
                      <h2 className="card-title"> n hng gn y</h2>
                      <Link to="/seller/orders" className="view-all-link">
                        Xem tt c 
                      </Link>
                    </div>
                    <div className="orders-list">
                      {orders.length > 0 ? (
                        orders.map(order => (
                          <div key={order.order_id} className="order-item">
                            <div className="order-header">
                              <span className="order-id">#{order.order_id}</span>
                              <span className={`order-status status-${order.status}`}>
                                {getStatusLabel(order.status)}
                              </span>
                            </div>
                            <div className="order-details">
                              <p className="order-customer"> {order.buyer_name}</p>
                              <p className="order-total"> ${order.total_amount}</p>
                              <p className="order-date"> {formatDate(order.created_at)}</p>
                            </div>
                            {order.status === 'pending' && (
                              <div className="order-actions">
                                <button 
                                  className="btn btn-sm btn-success"
                                  onClick={() => handleOrderStatusUpdate(order.order_id, 'processing')}
                                >
                                   Xc nhn
                                </button>
                                <button 
                                  className="btn btn-sm btn-danger"
                                  onClick={() => handleOrderStatusUpdate(order.order_id, 'cancelled')}
                                >
                                   Hy
                                </button>
                              </div>
                            )}
                          </div>
                        ))
                      ) : (
                        <div className="empty-state">
                          <p> Cha c n hng no</p>
                        </div>
                      )}
                    </div>
                  </div>

                </div>
              </div>
            )}

            {/* PRODUCTS TAB */}
            {activeTab === 'products' && (
              <div className="products-tab">
                <div className="tab-header">
                  <h2> Qun l sn phm</h2>
                  <Link to="/seller/products/new" className="btn btn-primary">
                     Thm sn phm mi
                  </Link>
                </div>
                
                <div className="products-grid">
                  {products.map(product => (
                    <div key={product.product_id} className="product-card">
                      <img 
                        src={product.image || '/placeholder.png'} 
                        alt={product.name}
                        className="product-image"
                      />
                      <div className="product-body">
                        <h3>{product.name}</h3>
                        <p className="product-description">{product.description?.substring(0, 100)}...</p>
                        <div className="product-meta">
                          <span className="price">${product.price}</span>
                          <span className="stock">Kho: {product.stock}</span>
                        </div>
                        <span className={`badge ${product.is_active ? 'badge-success' : 'badge-danger'}`}>
                          {product.is_active ? 'ang bn' : ' n'}
                        </span>
                      </div>
                      <div className="product-footer">
                        <button 
                          className="btn btn-sm btn-secondary"
                          onClick={() => navigate(`/seller/products/edit/${product.product_id}`)}
                        >
                           Sa
                        </button>
                        <button 
                          className="btn btn-sm btn-outline"
                          onClick={() => handleProductStatusToggle(product.product_id, product.is_active)}
                        >
                          {product.is_active ? ' n' : ' Hin'}
                        </button>
                      </div>
                    </div>
                  ))}
                </div>

                {products.length === 0 && (
                  <div className="empty-state-large">
                    <div className="empty-icon"></div>
                    <h3>Cha c sn phm no</h3>
                    <p>Bt u bn hng bng cch thm sn phm u tin ca bn</p>
                    <Link to="/seller/products/new" className="btn btn-primary">
                       Thm sn phm u tin
                    </Link>
                  </div>
                )}
              </div>
            )}

            {/* ORDERS TAB */}
            {activeTab === 'orders' && (
              <div className="orders-tab">
                <div className="tab-header">
                  <h2> Qun l n hng</h2>
                  <div className="filter-buttons">
                    <button className="btn btn-sm btn-outline active">Tt c</button>
                    <button className="btn btn-sm btn-outline">Ch x l</button>
                    <button className="btn btn-sm btn-outline">ang x l</button>
                    <button className="btn btn-sm btn-outline"> giao</button>
                  </div>
                </div>

                <div className="orders-table">
                  <table>
                    <thead>
                      <tr>
                        <th>M n</th>
                        <th>Khch hng</th>
                        <th>Sn phm</th>
                        <th>Tng tin</th>
                        <th>Trng thi</th>
                        <th>Ngy t</th>
                        <th>Thao tc</th>
                      </tr>
                    </thead>
                    <tbody>
                      {orders.map(order => (
                        <tr key={order.order_id}>
                          <td>#{order.order_id}</td>
                          <td>{order.buyer_name}</td>
                          <td>{order.items_count} sn phm</td>
                          <td className="price">${order.total_amount}</td>
                          <td>
                            <span className={`badge status-${order.status}`}>
                              {getStatusLabel(order.status)}
                            </span>
                          </td>
                          <td>{formatDate(order.created_at)}</td>
                          <td>
                            <div className="action-buttons">
                              <button 
                                className="btn-icon" 
                                title="Xem chi tit"
                                onClick={() => navigate(`/seller/orders/${order.order_id}`)}
                              >
                                
                              </button>
                              {order.status === 'pending' && (
                                <>
                                  <button 
                                    className="btn-icon" 
                                    title="Xc nhn"
                                    onClick={() => handleOrderStatusUpdate(order.order_id, 'processing')}
                                  >
                                    
                                  </button>
                                  <button 
                                    className="btn-icon" 
                                    title="Hy"
                                    onClick={() => handleOrderStatusUpdate(order.order_id, 'cancelled')}
                                  >
                                    
                                  </button>
                                </>
                              )}
                            </div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>

                  {orders.length === 0 && (
                    <div className="empty-state-large">
                      <div className="empty-icon"></div>
                      <h3>Cha c n hng no</h3>
                      <p>n hng s hin th  y khi c khch t mua</p>
                    </div>
                  )}
                </div>
              </div>
            )}

          </div>
        </div>
      </div>

      <Footer />
    </>
  );
};

// Helper functions
const getStatusLabel = (status) => {
  const labels = {
    pending: ' Ch x l',
    processing: ' ang x l',
    shipped: ' ang giao',
    delivered: '  giao',
    cancelled: '  hy',
  };
  return labels[status] || status;
};

const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('vi-VN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit'
  });
};

export default SellerDashboard;