import React, { useState, useEffect, useMemo } from 'react';
import { useAuth } from '../utils/AuthContext';
import { useNavigate } from 'react-router-dom';
import { 
  getCities, 
  getDistrictsByCity, 
  getWardsByDistrict,
  getCityName,
  getDistrictName,
  getWardName
} from '../utils/locationsData';
import { formatPrice } from '../utils/formatPrice';
import resolveAvatarUrl from '../utils/avatar';
import Icon from '../components/Icon';
import usePageTitle from '../hooks/usePageTitle';

import '../assets/UserProfile.css';

const API_BASE = process.env.REACT_APP_API_BASE || 'http://localhost:8000';

const Profile = () => {
  const { user, updateUser } = useAuth();
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('profile');
  const [isEditing, setIsEditing] = useState(false);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });

  const [formData, setFormData] = useState({
    full_name: '',
    phone: '',
    email: '',
    bio: '',
    address: '',
    city: '',
    district: '',
    ward: '',
    country: 'Vietnam',
  });

  const [cities, setCities] = useState([]);
  const [districts, setDistricts] = useState([]);
  const [wards, setWards] = useState([]);
  const [orders, setOrders] = useState([]);
  const [ordersLoading, setOrdersLoading] = useState(false);
  const [ordersError, setOrdersError] = useState('');

  const avatarSrc = useMemo(() => resolveAvatarUrl(user, '/default-avatar.png'), [user]);

  usePageTitle('H s ca ti');

  useEffect(() => {
    if (user) {
      setFormData({
        full_name: user.full_name || '',
        phone: user.phone || '',
        email: user.email || '',
        bio: user.profile?.bio || '',
        address: user.profile?.address || '',
        city: user.profile?.city || '',
        district: user.profile?.district || '',
        ward: user.profile?.ward || '',
        country: user.profile?.country || 'Vietnam',
      });
    }

    // Load cities from mock data
    setCities(getCities());
  }, [user]);

  useEffect(() => {
    if (formData.city) {
      const districtsList = getDistrictsByCity(formData.city);
      setDistricts(districtsList);
      
      // Reset district and ward if city changes
      if (!districtsList.find(d => d.id === formData.district)) {
        setFormData(prev => ({ ...prev, district: '', ward: '' }));
      }
    } else {
      setDistricts([]);
      setWards([]);
    }
  }, [formData.city, formData.district]);

  useEffect(() => {
    if (formData.district) {
      const wardsList = getWardsByDistrict(formData.district);
      setWards(wardsList);
      
      // Reset ward if district changes
      if (!wardsList.find(w => w.id === formData.ward)) {
        setFormData(prev => ({ ...prev, ward: '' }));
      }
    } else {
      setWards([]);
    }
  }, [formData.district, formData.ward]);

  useEffect(() => {
    if (activeTab === 'orders') {
      const token = localStorage.getItem('access_token');
      if (!token) {
        setOrders([]);
        setOrdersError('Bn cha ng nhp.');
        return;
      }
      setOrdersLoading(true);
      setOrdersError('');
      fetch(`${API_BASE}/api/orders/mine/`, {
        headers: { Authorization: `Bearer ${token}` }
      })
        .then(async res => {
          const ct = res.headers.get('content-type') || '';
          if (!res.ok) throw new Error(`HTTP ${res.status}`);
          if (!ct.includes('application/json')) throw new Error('Phn hi khng phi JSON');
          return res.json();
        })
        .then(data => {
          setOrders(Array.isArray(data.results) ? data.results : []);
        })
        .catch(err => setOrdersError(err.message))
        .finally(() => setOrdersLoading(false));
    }
  }, [activeTab]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    setMessage({ type: '', text: '' });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ type: '', text: '' });

    try {
      const updateData = {
        full_name: formData.full_name,
        phone: formData.phone,
        profile: {
          bio: formData.bio,
          address: formData.address,
          city: formData.city,
          district: formData.district,
          ward: formData.ward,
          country: formData.country,
        }
      };

      console.log('Sending update data:', updateData);

      const response = await fetch(`${API_BASE}/api/users/profile/`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('access_token')}`
        },
        body: JSON.stringify(updateData)
      });

      console.log('Response status:', response.status);

      const contentType = response.headers.get('content-type') || '';
      let result = {};
      if (contentType.includes('application/json')) {
        result = await response.json();
      } else {
        const text = await response.text();
        if (text?.trim()) {
          try {
            result = JSON.parse(text);
          } catch (err) {
            throw new Error(text);
          }
        }
      }
      console.log('Response data:', result);

      if (!response.ok) {
        throw new Error(result.message || result.detail || 'Cp nht tht bi');
      }

      if (result.user) {
        updateUser(result.user);
      } else if (user) {
        const updatedProfile =
          typeof result === 'object' && result !== null
            ? (result.profile && typeof result.profile === 'object'
                ? result.profile
                : result)
            : {};

        updateUser({
          ...user,
          full_name: updateData.full_name,
          phone: updateData.phone,
          profile: {
            ...user.profile,
            ...updateData.profile,
            ...updatedProfile,
          },
        });
      }
      
      setMessage({ 
        type: 'success', 
        text: result.message || 'Cp nht thng tin thnh cng!' 
      });
      setIsEditing(false);
    } catch (error) {
      console.error('Update error:', error);
      setMessage({ 
        type: 'error', 
        text: error.message || 'Cp nht tht bi. Vui lng th li!' 
      });
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    if (user) {
      setFormData({
        full_name: user.full_name || '',
        phone: user.phone || '',
        email: user.email || '',
        bio: user.profile?.bio || '',
        address: user.profile?.address || '',
        city: user.profile?.city || '',
        district: user.profile?.district || '',
        ward: user.profile?.ward || '',
        country: user.profile?.country || 'Vietnam',
      });
    }
    setIsEditing(false);
    setMessage({ type: '', text: '' });
  };

  const statusMap = {
    pending: 'Ch x l',
    paid: ' thanh ton',
    shipping: 'ang giao',
    completed: 'Hon tt',
    canceled: ' hy'
  };

  const renderProfileTab = () => (
    <div className="profile-form">
      {message.text && (
        <div className={`alert ${message.type === 'success' ? 'alert-success' : 'alert-error'}`}>
          {message.text}
        </div>
      )}

      <div className="form-section">
        <h3>nh i din</h3>
        <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
          <img
            src={avatarSrc}
            alt={formData.full_name || user?.username || 'avatar'}
            style={{ width: 96, height: 96, borderRadius: '50%', objectFit: 'cover', border: '2px solid #eee' }}
          />
          <p style={{ color: '#6b7280', fontSize: 14 }}>
            nh i din c ng b  mi ni. Cp nht avatar ti mc thng tin ti khon trong tng lai.
          </p>
        </div>
      </div>

      <div className="form-section">
        <h3>Thng tin c nhn</h3>
        <div className="form-row">
          <div className="form-group">
            <label>H v tn</label>
            {isEditing ? (
              <input
                type="text"
                name="full_name"
                value={formData.full_name}
                onChange={handleChange}
                placeholder="Nhp h v tn"
              />
            ) : (
              <p>{formData.full_name || 'Cha cp nht'}</p>
            )}
          </div>

          <div className="form-group">
            <label>S in thoi</label>
            {isEditing ? (
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                placeholder="Nhp s in thoi"
              />
            ) : (
              <p>{formData.phone || 'Cha cp nht'}</p>
            )}
          </div>
        </div>

        <div className="form-group">
          <label>Email</label>
          <p>{formData.email}</p>
        </div>

        <div className="form-group">
          <label>Gii thiu</label>
          {isEditing ? (
            <textarea
              name="bio"
              value={formData.bio}
              onChange={handleChange}
              placeholder="Vit vi dng v bn thn..."
              rows="4"
            />
          ) : (
            <p>{formData.bio || 'Cha c gii thiu'}</p>
          )}
        </div>
      </div>

      <div className="form-section">
        <h3>a ch</h3>
        
        {isEditing ? (
          <>
            <div className="form-row">
              <div className="form-group">
                <label>Tnh/Thnh ph</label>
                <select
                  name="city"
                  value={formData.city}
                  onChange={handleChange}
                >
                  <option value="">Chn tnh/thnh ph</option>
                  {cities.map(city => (
                    <option key={city.id} value={city.id}>
                      {city.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="form-group">
                <label>Qun/Huyn</label>
                <select
                  name="district"
                  value={formData.district}
                  onChange={handleChange}
                  disabled={!formData.city}
                >
                  <option value="">Chn qun/huyn</option>
                  {districts.map(district => (
                    <option key={district.id} value={district.id}>
                      {district.name}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label>Phng/X</label>
                <select
                  name="ward"
                  value={formData.ward}
                  onChange={handleChange}
                  disabled={!formData.district}
                >
                  <option value="">Chn phng/x</option>
                  {wards.map(ward => (
                    <option key={ward.id} value={ward.id}>
                      {ward.name}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div className="form-group">
              <label>a ch chi tit</label>
              <input
                type="text"
                name="address"
                value={formData.address}
                onChange={handleChange}
                placeholder="S nh, tn ng..."
              />
            </div>
          </>
        ) : (
          <>
            <div className="form-group">
              <label>a ch y </label>
              <p>
                {formData.address && formData.address}
                {formData.ward && `, ${getWardName(formData.ward)}`}
                {formData.district && `, ${getDistrictName(formData.district)}`}
                {formData.city && `, ${getCityName(formData.city)}`}
                {!formData.address && 'Cha cp nht'}
              </p>
            </div>
          </>
        )}
      </div>
    </div>
  );

  const renderOrdersTab = () => {
    if (ordersLoading) return <div className="loading-box">ang ti n hng...</div>;
    if (ordersError) return <div className="alert alert-error">{ordersError}</div>;
    if (!orders.length) {
      return (
        <div className="empty-orders">
          <div className="empty-icon">
            <Icon name="box-open" size={48} />
          </div>
            <h3>Cha c n hng no</h3>
            <p>Bn cha c n hng. Hy mua sm ngay!</p>
            <a href="/" className="browse-products-btn">Khm ph sn phm</a>
        </div>
      );
    }
    return (
      <div className="orders-list">
        {orders.map(o => {
          const createdDate = new Date(o.created_at);
          const itemsPreview = o.items?.slice(0, 3) || [];
          const remainingItems = Math.max((o.items?.length || 0) - itemsPreview.length, 0);
          const paymentLabel = o.payment_method === 'cod' ? 'Thanh ton COD' : (o.payment_method || 'Khc');

          return (
            <div key={o.order_id} className="order-card">
              <div className="order-card-header">
                <div className="order-code">
                  <span>M n</span>
                  <p>#{o.order_id}</p>
                </div>
                <span className={`order-status-pill status-${o.status}`}>
                  {statusMap[o.status] || o.status}
                </span>
              </div>

              <div className="order-card-meta">
                <div className="meta-block">
                  <p>Ngy t</p>
                  <strong>
                    {createdDate.toLocaleDateString('vi-VN')}<br />
                    <span>{createdDate.toLocaleTimeString('vi-VN')}</span>
                  </strong>
                </div>
                <div className="meta-block">
                  <p>Thanh ton</p>
                  <strong>{paymentLabel}</strong>
                </div>
                <div className="meta-block">
                  <p>Tng tin</p>
                  <strong>{formatPrice(o.total_amount)}</strong>
                </div>
              </div>

              <div className="order-items-preview">
                {itemsPreview.map((it, idx) => (
                  <div key={idx} className="order-item-chip">
                    <div>
                      <p>{it.product}</p>
                      <span>SL {it.quantity}</span>
                    </div>
                    <span>{formatPrice((it.price || 0) * (it.quantity || 0))}</span>
                  </div>
                ))}
                {remainingItems > 0 && (
                  <div className="order-items-more">
                    +{remainingItems} sn phm khc
                  </div>
                )}
              </div>

              <div className="order-card-footer">
                <div className="order-destination">
                  <span>Giao n</span>
                  <p>{o.shipping_address || o.delivery_address || 'ang cp nht'}</p>
                </div>
                <button
                  onClick={() => navigate(`/orders/${o.order_id}`)}
                  className="order-detail-btn"
                >
                  Xem chi tit
                </button>
              </div>
            </div>
          );
        })}
      </div>
    );
  };


  return (
    <div className="user-profile-page">
      <div className="profile-container">
        <aside className="profile-sidebar">
          <div className="user-info">
            <div className="avatar">
              {user?.avatar ? (
                <img src={user.avatar} alt={user.full_name} />
              ) : (
                <div className="avatar-placeholder">
                  {user?.full_name?.charAt(0) || user?.username?.charAt(0) || '?'}
                </div>
              )}
            </div>
            <h3>{user?.full_name || user?.username}</h3>
            <p>{user?.email}</p>
            <p className="member-since">
              Thnh vin t {new Date(user?.date_joined).toLocaleDateString('vi-VN')}
            </p>
          </div>

          <nav className="profile-nav">
            <button
              className={`nav-item ${activeTab === 'profile' ? 'active' : ''}`}
              onClick={() => setActiveTab('profile')}
            >
              Thng tin c nhn
            </button>
            <button
              className={`nav-item ${activeTab === 'orders' ? 'active' : ''}`}
              onClick={() => setActiveTab('orders')}
            >
              n hng ca ti
            </button>
            <button
              className="nav-item"
              onClick={() => navigate('/change-password')}
            >
              i mt khu
            </button>
          </nav>
        </aside>

        <main className="profile-content">
          <div className="tab-header">
            <h2>
              {activeTab === 'profile' && 'Thng tin c nhn'}
              {activeTab === 'orders' && 'n hng ca ti'}
            </h2>
            
            {activeTab === 'profile' && (
              <div className="edit-actions">
                {isEditing ? (
                  <>
                    <button 
                      className="save-btn"
                      onClick={handleSubmit}
                      disabled={loading}
                    >
                      {loading ? 'ang lu...' : 'Lu thay i'}
                    </button>
                    <button 
                      className="cancel-btn"
                      onClick={handleCancel}
                      disabled={loading}
                    >
                      Hy
                    </button>
                  </>
                ) : (
                  <button 
                    className="edit-btn"
                    onClick={() => setIsEditing(true)}
                  >
                    Chnh sa
                  </button>
                )}
              </div>
            )}
          </div>

          {activeTab === 'profile' && renderProfileTab()}
          {activeTab === 'orders' && renderOrdersTab()}
        </main>
      </div>
    </div>
  );
};

export default Profile;